import React from 'react';
import { TabBar } from 'antd-mobile';
import Home from './Home';
import Idea from './Idea';
import Mall from './Mall';

export default class AppTab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'home',
        };
    }
    render() {
        return (
            <div style={{ position: 'fixed', height: '100%', width: '100%', top: 0 }}>
                <TabBar
                    unselectedTintColor="#949494"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="È¶ñÈ°µ"
                        key="Home"
                        icon={<i style={{ fontSize: 22, lineHeight: '22px' }} className='iconfont icon-home'></i>
                        }
                        selectedIcon={
                            <i style={{ fontSize: 22, lineHeight: '22px' }} className='iconfont icon-wode'></i>
                        }
                        selected={this.state.selectedTab === 'home'}
                        onPress={() => {
                            this.setState({
                                selectedTab: 'home',
                            });
                        }}
                    >
                        <Home />
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <div style={{
                                width: '22px',
                                height: '22px',
                                background: 'url(https://gw.alipayobjects.com/zos/rmsportal/BTSsmHkPsQSPTktcXyTV.svg) center center /  21px 21px no-repeat'
                            }}
                            />
                        }
                        selectedIcon={
                            <div style={{
                                width: '22px',
                                height: '22px',
                                background: 'url(https://gw.alipayobjects.com/zos/rmsportal/ekLecvKBnRazVLXbWOnE.svg) center center /  21px 21px no-repeat'
                            }}
                            />
                        }
                        title="ÁÅµÊÑü"
                        key="Idea"
                        selected={this.state.selectedTab === 'idea'}
                        onPress={() => {
                            this.setState({
                                selectedTab: 'idea',
                            });
                        }}
                    >
                        <Idea />
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <div style={{
                                width: '22px',
                                height: '22px',
                                background: 'url(https://zos.alipayobjects.com/rmsportal/psUFoAMjkCcjqtUCNPxB.svg) center center /  21px 21px no-repeat'
                            }}
                            />
                        }
                        selectedIcon={
                            <div style={{
                                width: '22px',
                                height: '22px',
                                background: 'url(https://zos.alipayobjects.com/rmsportal/IIRLrXXrFAhXVdhMWgUI.svg) center center /  21px 21px no-repeat'
                            }}
                            />
                        }
                        title="ÂïÜÂüé"
                        key="Mall"
                        dot
                        selected={this.state.selectedTab === 'mall'}
                        onPress={() => {
                            this.setState({
                                selectedTab: 'mall',
                            });
                        }}
                    >
                        <Mall />
                    </TabBar.Item>
                    <TabBar.Item
                        icon={{ uri: 'https://zos.alipayobjects.com/rmsportal/asJMfBrNqpMMlVpeInPQ.svg' }}
                        selectedIcon={{ uri: 'https://zos.alipayobjects.com/rmsportal/gjpzzcrPMkhfEqgbYvmN.svg' }}
                        title="ÊàëÁöÑ"
                        key="my"
                        selected={this.state.selectedTab === 'yellowTab'}
                        onPress={() => {
                            this.setState({
                                selectedTab: 'yellowTab',
                            });
                        }}
                    >
                        ÊàëÁöÑ
          </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}